.Login {
    background-color: #f8f8f8;
    height: 100vh;
    width: 100vw;
    display: grid;
    place-items: center;

    &__Container {
        padding: 100px;
        text-align: center;
        background-color: #ffffff;
        border-radius: 10px;
        box-shadow: 0 1px 3px rgba(0,0,0,.12); }
    &__Logo {
        object-fit: contain;
        height: 100px;
        margin-bottom: 40px; }
    &__Action {
        margin-top: 50px;
        text-transform: inherit !important;
        background-color: #0a8d48 !important;
        color: white !important; } }
